// Variables
$background-color: rgba(51, 51, 51, 0.05);
$text-color: #333;
$font-size-normal: 1rem;
$font-size-small: 0.9rem;
$font-weight: 500;

.App {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  color: $text-color;
  font-size: $font-size-normal;
  font-weight: $font-weight;
}

// Login
.login {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100vh;

  .profile-pic-div {
    display: flex;
    justify-content: center;

    .profile-pic {
      width: 7rem;
      height: 7rem;
    }
  }

  .image-upload-input {
    border-color: rgba(51, 51, 51, 0.01);
  }

  input,
  .image-upload-input {
    background: $background-color;
  }

  .login-button-div {
    display: flex;
    justify-content: center;

    .login-button {
      background-color: #ffffff;
      border: 1px solid rgba(0, 0, 0, 0.1);
      border-radius: 0.25rem;
      align-items: center;
      box-shadow: rgba(0, 0, 0, 0.02) 0 1px 3px 0;
      box-sizing: border-box;
      color: $text-color;
      cursor: pointer;
      display: inline-flex;
      justify-content: center;
      margin: 0;
      position: relative;
      text-decoration: none;
      transition: all 250ms;
      width: auto;
      font-family: system-ui, -apple-system, system-ui, "Helvetica Neue",
        Helvetica, Arial, sans-serif;
      font-size: $font-size-normal;
      font-weight: $font-weight;
      line-height: 20px;
      list-style: none;
      margin: 0;
      padding: 10px 12px;
      text-align: center;
      transition: all 200ms;
      vertical-align: baseline;
      white-space: nowrap;
      user-select: none;
      -webkit-user-select: none;
      touch-action: manipulation;
    }
  }

  .login-button:hover,
  .login-button:focus {
    border-color: rgba(0, 0, 0, 0.15);
    box-shadow: rgba(0, 0, 0, 0.1) 0 4px 12px;
    color: rgba(0, 0, 0, 0.65);
  }

  .login-button:hover {
    transform: translateY(-1px);
  }

  .login-button:active {
    background-color: #f0f0f1;
    border-color: rgba(0, 0, 0, 0.15);
    box-shadow: rgba(0, 0, 0, 0.06) 0 2px 4px;
    color: rgba(0, 0, 0, 0.65);
    transform: translateY(1px);
  }
}

// Header
.header {
  .search * {
    background: $background-color;

    .search-icon {
      background: none;
    }
  }
}

// Main
.main {
  height: 100%;
  width: 100%;

  .event-item {
    border-radius: 0.5rem;
    padding: 0.5rem 1.5rem;

    .event-head * {
      padding: 0.1rem;
    }

    .event-body {
      font-size: $font-size-small;
    }
  }

  .event-item:hover {
    background: $background-color;
  }

  .person {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;

    .profile-pic-div {
      display: flex;
      justify-content: center;

      .profile-pic {
        width: 7rem;
        height: 7rem;
      }
    }

    .image-upload-input {
      border-color: rgba(51, 51, 51, 0.01);
    }

    input,
    textarea,
    .image-upload-input {
      background: $background-color;
    }

    .update-button-div {
      display: flex;
      justify-content: center;

      .update-button,
      .save-button,
      .cancel-button {
        background-color: #ffffff;
        border: 1px solid rgba(0, 0, 0, 0.1);
        border-radius: 0.25rem;
        align-items: center;
        box-shadow: rgba(0, 0, 0, 0.02) 0 1px 3px 0;
        box-sizing: border-box;
        color: $text-color;
        cursor: pointer;
        display: inline-flex;
        justify-content: center;
        margin: 0;
        position: relative;
        text-decoration: none;
        width: auto;
        font-family: system-ui, -apple-system, system-ui, "Helvetica Neue",
          Helvetica, Arial, sans-serif;
        font-size: $font-size-normal;
        font-weight: $font-weight;
        line-height: 20px;
        list-style: none;
        padding: 10px 12px;
        text-align: center;
        vertical-align: baseline;
        white-space: nowrap;
        user-select: none;
        -webkit-user-select: none;
        touch-action: manipulation;
      }

      .save-button,
      .cancel-button {
        margin: 0 1rem;
      }
    }

    .update-button:hover,
    .save-button:hover,
    .cancel-button:hover,
    .save-button:focus,
    .cancel-button:focus,
    .update-button:focus {
      border-color: rgba(0, 0, 0, 0.15);
      box-shadow: rgba(0, 0, 0, 0.1) 0 4px 12px;
      color: rgba(0, 0, 0, 0.65);
    }

    .update-button:hover,
    .save-button:hover,
    .cancel-button:hover {
      transform: translateY(-1px);
    }

    .update-button:active,
    .save-button:active,
    .cancel-button:active {
      background-color: #f0f0f1;
      border-color: rgba(0, 0, 0, 0.15);
      box-shadow: rgba(0, 0, 0, 0.06) 0 2px 4px;
      color: rgba(0, 0, 0, 0.65);
      transform: translateY(1px);
    }
  }
}

// Utility Class
.hidden {
  display: hidden;
}

// Footer
.footer {
  background: #fff;
}
