.App {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  color: #333;
  font-size: 1rem;
  font-weight: 500;
}

.login {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100vh;
}
.login .profile-pic-div {
  display: flex;
  justify-content: center;
}
.login .profile-pic-div .profile-pic {
  width: 7rem;
  height: 7rem;
}
.login .image-upload-input {
  border-color: rgba(51, 51, 51, 0.01);
}
.login input,
.login .image-upload-input {
  background: rgba(51, 51, 51, 0.05);
}
.login .login-button-div {
  display: flex;
  justify-content: center;
}
.login .login-button-div .login-button {
  background-color: #ffffff;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 0.25rem;
  align-items: center;
  box-shadow: rgba(0, 0, 0, 0.02) 0 1px 3px 0;
  box-sizing: border-box;
  color: #333;
  cursor: pointer;
  display: inline-flex;
  justify-content: center;
  margin: 0;
  position: relative;
  text-decoration: none;
  transition: all 250ms;
  width: auto;
  font-family: system-ui, -apple-system, system-ui, "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 1rem;
  font-weight: 500;
  line-height: 20px;
  list-style: none;
  margin: 0;
  padding: 10px 12px;
  text-align: center;
  transition: all 200ms;
  vertical-align: baseline;
  white-space: nowrap;
  -moz-user-select: none;
   -ms-user-select: none;
       user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}
.login .login-button:hover,
.login .login-button:focus {
  border-color: rgba(0, 0, 0, 0.15);
  box-shadow: rgba(0, 0, 0, 0.1) 0 4px 12px;
  color: rgba(0, 0, 0, 0.65);
}
.login .login-button:hover {
  transform: translateY(-1px);
}
.login .login-button:active {
  background-color: #f0f0f1;
  border-color: rgba(0, 0, 0, 0.15);
  box-shadow: rgba(0, 0, 0, 0.06) 0 2px 4px;
  color: rgba(0, 0, 0, 0.65);
  transform: translateY(1px);
}

.header .search * {
  background: rgba(51, 51, 51, 0.05);
}
.header .search * .search-icon {
  background: none;
}

.main {
  height: 100%;
  width: 100%;
}
.main .event-item {
  border-radius: 0.5rem;
  padding: 0.5rem 1.5rem;
}
.main .event-item .event-head * {
  padding: 0.1rem;
}
.main .event-item .event-body {
  font-size: 0.9rem;
}
.main .event-item:hover {
  background: rgba(51, 51, 51, 0.05);
}
